<template>
	<div id="map"></div>
</template>

<script>
window._AMapSecurityConfig = {
	securityJsCode: 'cc881cb79056e47dc5747eeb0a525791'
}
import { defineComponent, onMounted, ref, reactive } from 'vue'
import AMapLoader from '@amap/amap-jsapi-loader'
export default defineComponent({
	// 简单功能的实现
	setup() {
		const companyName = JSON.parse(localStorage.getItem('company')).companyName
		const address = JSON.parse(localStorage.getItem('company')).extras.filter(item => { return item.extraName == '地址'})[0].extraValue
		// 初始化地图
		function initMap() {
			AMapLoader.load({
				key: "43fd4553a659e06362251f570cb87019", // 申请好的Web端开发者Key，首次调用 load 时必填
				version:'2.0',
				AMapUI: {
					version: '1.1',
					plugins:['overlay/SimpleMarker']
				},
				plugins: [
					"AMap.Scale", //工具条，控制地图的缩放、平移等
					"AMap.ToolBar", //比例尺，显示当前地图中心的比例尺
					"AMap.Geocoder",
					"AMap.Geolocation", //定位，提供了获取用户当前准确位置、所在城市的方法
				]
			})
			.then((AMap)=>{
				AMap.plugin('AMap.Geocoder', function() {
					var geocoder = new AMap.Geocoder();
					geocoder.getLocation(address, function(status, result) {
						if (result && result.geocodes && result.geocodes.length) {
							var lnglat = result.geocodes[0].location;
							map = new AMap.Map('map',{
							    zoom: 14,
									center: [lnglat.lng,lnglat.lat]
							});
							AMapUI.loadUI(['overlay/SimpleInfoWindow'], function(SimpleInfoWindow) {
								var marker = new AMap.Marker({
									map: map,
									zIndex: 9999999,
									position: [lnglat.lng,lnglat.lat]
								});
								
								var infoWindow = new SimpleInfoWindow({
									infoTitle: `<strong>${companyName}</strong>`,
									infoBody: `<strong>${address}</strong>`,
									//基点指向marker的头部位置
									offset: new AMap.Pixel(0, -31)
								});
								function openInfoWin() {
									infoWindow.open(map, marker.getPosition());
								}
								//marker 点击时打开
								marker.on(marker, 'click', function() {
									openInfoWin();
								});
								openInfoWin();
							});
						}
					});
				  // 使用geocoder做地理/逆地理编码
				})
				
			})
		}
		// 调用地图初始化函数：onMounted 函数会在 DOM 初始化完成后调用，建议在 mounted 函数中调用地图初始化方法
		onMounted(() => {
			initMap();
		});
	
	  return { initMap };
	}
})
</script>

<style scoped>
	#map {
		width: 807px;
		height: 387px;
	}
	#searchResults {
		width: 100px;
		height: 200px;
	}
</style>