const rules = [
	{ 
		name: 'email',
		rule: [
			{
				trigger: 'blur',
				validator: async (rule, value) => {
					let reg = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
					return !value ? Promise.resolve() : !reg.test(value) ? Promise.reject("请输入正确邮箱地址") : Promise.resolve()
				}
			}
		]
	},
	{
		name: 'phone',
		rule: [
			{
				trigger: 'blur',
				validator: async (rule, value) => {
					let reg = /^1[3456789]\d{9}$/
					return value && !reg.test(value) ? Promise.reject("请输入正确手机号") : Promise.resolve()
				}
			}
		]
	}
]

export default {
	rules
}