					<a-breadcrumb-item href="">
						<span>联系我们</span>
					</a-breadcrumb-item>
				</a-breadcrumb>
			</div>
		</div>
		<!-- 地图 -->
		<div class="us-box">
			<div class="content-box flex-row">
				<div class="us-left">
					<div class="us-title">
						<div>{{pageState.companyName}}</div>
					</div>
					<div class="us-info">
						<div class="flex-row" v-if="pageState.phone">
							<img src="@/assets/phone.png" alt="">
							<span>电话：{{pageState.phone}}</span>
						</div>
						<div class="flex-row" v-if="pageState.code">
							<img src="@/assets/code.png" alt="">
							<span>邮编：{{pageState.code}}</span>
						</div>
						<div class="flex-row" v-if="pageState.email">
							<img src="@/assets/email.png" alt="">
							<span>邮箱：{{pageState.email}}</span>
						</div>
						<div class="flex-row" v-if="pageState.address">
							<img src="@/assets/address.png" alt="">
							<span>地址：{{pageState.address}}</span>
						</div>
					</div>
				</div>
				<Map></Map>
				<!-- <img class="map" src="@/assets/swiper.png" alt=""> -->
			</div>
		</div>
		<!-- 留言 -->
		<div class="message-box">
			<div class="content-box">
				<div class="message-title">
					<div>在线留言</div>
					<div>Online Message</div>
				</div>
				<div class="message-form">
					<div class="flex-row">
					<a-form
						ref="formRef"
						:model="formState"
						:scrollToFirstError="true"
					>
						<div 
							class="message-form-item" 
							v-for="(item, index) of pageState.form"
							:key="index"
						>
							<a-form-item class="message-form-item-box" :rules="item.rule" :ref="item.consultTitle" :name="item.consultTitle">
								<a-input 
									v-if="item.domElement == 'input'" 
									:type="item.paramType"
									v-model:value="formState[item.consultTitle]"
									:placeholder="`请填写${item.consultTitle}`"
								/>
								<a-textarea
									v-if="item.domElement == 'textarea'"
									v-model:value="formState[item.consultTitle]"
									:placeholder="item.consultTitle"
								/>
							</a-form-item>
						</div>
					</a-form>
						
					</div>
					
				</div>
				<div class="submit" @click="onSubmit">提交</div>
			</div>
		</div>
	</div>
</template>

<script>
import { defineComponent, onMounted, ref, reactive } from 'vue'
import { message } from 'ant-design-vue';
import { useRoute } from 'vue-router'
import { HomeOutlined } from '@ant-design/icons-vue'
import { selectFormInfo, updateForm } from '@/api/api.js'
import rules from '@/utils/rules.js'
import eventBus from '@/utils/eventBus.js'
import Map from '@/components/map.vue'
export default defineComponent({
  components: {
		HomeOutlined,
		Map
  },
	setup () {
		const formRef = ref(), formState = reactive({})
		const route = useRoute()
		const pageState = ref({
			form: []
		})
		if (localStorage.getItem('company')) {
			let company = JSON.parse(localStorage.getItem('company'))
			pageState.value.companyName = company.companyName
			pageState.value.phone = company.extras.filter(item => { return item.extraName == '联系电话'})[0] ? company.extras.filter(item => { return item.extraName == '联系电话'})[0].extraValue : null
			pageState.value.code = company.extras.filter(item => { return item.extraName == '邮编'})[0] ? company.extras.filter(item => { return item.extraName == '邮编'})[0].extraValue : null
			pageState.value.email = company.extras.filter(item => { return item.extraName == '邮箱'})[0] ? company.extras.filter(item => { return item.extraName == '邮箱'})[0].extraValue : null
			pageState.value.address = company.extras.filter(item => { return item.extraName == '地址'})[0] ? company.extras.filter(item => { return item.extraName == '地址'})[0].extraValue : null
		}
		onMounted(() => {
			if (route.path == '/') {
				localStorage.setItem('nav', 0)
				eventBus.$emit('changeNav', 0)
			}
			selectFormInfo(route.query.id).then((res) => {
				if (res.code === 200) {
					pageState.value.form = res.data.map(item => ({
						...item,
						rule: rules.rules.filter(r => { return r.name == item.paramType })[0] ? rules.rules.filter(r => { return r.name == item.paramType })[0].rule : [{required: item.required == 1 ? true : false, message: `请填写${item.consultTitle}`}]
					}))
				}
			})
		})
		const onSubmit = () => {
			formRef.value
				.validate()
				.then(() => {
					updateForm({content: JSON.stringify(formState)}).then((res) => {
						if (res.code === 200) {
							message.success({
								content: () => '留言成功',
								style: { marginTop: '20vh' },
							});
						} else {
							message.warn({
								content: () => res.msg,
								class: 'warn-class',
								style: {
									marginTop: '20vh',
								},
							});
						}
					})
				}).catch((e) => {
				})
		}
		return {
			pageState,
			formState,
			formRef,
			onSubmit
		}
	}
})
</script>

